import React, { useEffect, useState } from 'react';
import './ErrorMessage.scss';

export const ErrorMessage: React.FC = () => {
  const [isLife, setIsLife] = useState(true);

  useEffect(() => {
    setIsLife(true);

    setTimeout(() => {
      setIsLife(false);
    }, 3000);
  }, []);

  return (
    <>
      {isLife && (
        <div className="error">❌ No correct query ...</div>
      )}
    </>
  );
};
