import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useLocalStorage } from './utils/useLocalStorage';
import { createCardOfWeather } from './utils/createCardOfWeather';
import { actions as cardsActions } from './store/reducers/cardsReducer';
import { getByLoacation, getByName } from './api/weather';
import { CardStatistics } from './types/CardStatistics';
import { ListOfCards } from './components/ListOfCards';
import { Select } from './components/Select';
import { Search } from './components/Search';
import { ErrorMessage } from './components/ErrorMessage';
import './App.scss';

export const App: React.FC = () => {
  const [loadCardIsError, setLoadCardIsError] = useState(false);
  const [location, setLocation] = useLocalStorage<any>('location', {});

  const dispatch = useDispatch();
  const addCard = (cardToAdd: CardStatistics) => dispatch(cardsActions.add(cardToAdd));

  const handleLoadCard = async (searchQuery: string) => {
    try {
      if (searchQuery) {
        const data = await getByName(searchQuery);
        const card = createCardOfWeather(data);

        addCard(card);
      } else {
        const { latitude, longitude } = location;
        const data = await getByLoacation(latitude, longitude);
        const card = createCardOfWeather(data);

        addCard(card);
      }
    } catch (err) {
      setLoadCardIsError(true);

      setTimeout(() => {
        setLoadCardIsError(false);
      }, 3000);
    }
  };

  const getLocation = async () => {
    navigator.geolocation.getCurrentPosition((position) => {
      setLocation(position.coords);
    });
  };

  useEffect(() => {
    getLocation();
  }, []);

  return (
    <>
      <header className="header">
        <Search loadCard={handleLoadCard} />

        {loadCardIsError && (
          <ErrorMessage />
        )}

        <Select />
      </header>

      <main>
        <ListOfCards />
      </main>
    </>
  );
};
