import React from 'react';
import { useTranslation } from 'react-i18next';
import './Button.scss';

interface Props {
  onPressed: (str: string) => void;
  searchQuery: string;
}

export const Button: React.FC<Props> = ({ onPressed, searchQuery }) => {
  const { t } = useTranslation();

  return (
    <button
      type="button"
      className="button"
      onClick={() => onPressed(searchQuery)}
    >
      {t('Add')}
    </button>
  );
};
