import React, { useRef, useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { useLocalStorage } from '../../utils/useLocalStorage';
import { Dropdown } from '../Dropdown/Dropdown';
import { Button } from '../Button';
import { CardStatistics } from '../../types/CardStatistics';
import './Search.scss';
import { useAppSelector } from '../../store/hooks';

interface Props {
  loadCard: (str: string) => void;
}

export const Search: React.FC<Props> = ({ loadCard }) => {
  const cards = useAppSelector((state) => state.cards);
  const { t } = useTranslation();
  const [searchQuery, setSearchQuery] = useState('');
  const [dropdownList, setDropdownList] = useLocalStorage<string[]>('searchList', []);
  const [dropdownIsOpened, setDropdownIsOpened] = useState(false);
  const dropdownRef = useRef<HTMLDivElement>(null);

  const handleClearSearch = () => {
    setSearchQuery('');
  };

  const handleSetSearch = (str: string) => {
    setSearchQuery(str);
  };

  const addItemToDropdown = (card: CardStatistics) => {
    const item = `${card.name}, ${card.country}`;

    if (!dropdownList.includes(item)) {
      setDropdownList([...dropdownList, item]);
    }
  };

  const handleOpenDropdown = () => {
    setDropdownIsOpened(true);
  };

  const handleCloseDropdown = () => {
    setDropdownIsOpened(false);
  };

  const handleCloseDropdownOutside = (event: MouseEvent) => {
    if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
      handleCloseDropdown();
    }
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleCloseDropdownOutside);
  }, []);

  useEffect(() => {
    cards.forEach(card => addItemToDropdown(card));
    handleClearSearch();
  }, [cards]);

  return (
    <div className="search">
      <input
        type="text"
        className="search__box"
        placeholder={t('placeholder')}
        value={searchQuery}
        onClick={handleOpenDropdown}
        onChange={(event) => setSearchQuery(event.target.value)}
      />

      <div className="search__list" ref={dropdownRef}>
        <Dropdown
          isOpened={dropdownIsOpened}
          dropdownList={dropdownList}
          onSetSearch={handleSetSearch}
        />
      </div>

      <div className="search__button">
        <Button onPressed={loadCard} searchQuery={searchQuery} />
      </div>
    </div>
  );
};
