/* eslint-disable no-return-assign */
/* eslint-disable no-param-reassign */
import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { CardStatistics } from '../../types/CardStatistics';

const cardsSlice = createSlice({
  name: 'cards',
  initialState: [],
  reducers: {
    add: (cards: CardStatistics[], action: PayloadAction<CardStatistics>) => {
      cards.push(action.payload);
    },
    delete: (cards: CardStatistics[], action: PayloadAction<string>) => {
      const filteredCard = cards.filter((card) => card.id !== action.payload);

      cards.length = 0;
      cards.push(...filteredCard);
    },
    clear: () => [],
  },
});

export default cardsSlice.reducer;
export const { actions } = cardsSlice;
